.hero-container {
  width: 100%;
  padding: 16px 16px 35px;
  
}

.hero_content-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 32px;
}

.hero_poster-container {
  width: 100%;
  max-width: 400px;
}

.hero-poster {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.hero_content_text-container {

  width: 100%;
}

.hero_text-content {
  display: flex;
  flex-direction: column;
  gap: 24px;
  max-width: 700px;
  font-size: clamp(18px, 4vw, 22px);
  font-weight: 300;
  text-align: left;
  margin: 0;
}

.hero_text-content p {
  margin: 0;
  line-height: 1.5;
}

