.header-container {
    width: 100%;
    padding: 26px 16px 0px 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  
  .logo-container {
    display: flex;
  }
  
  .logo {
    width: 150px;
    height: 150px;
  }

  .toggLangBtn{
    width: 30px;
    height: 30px;
    border: 3px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 18px;
    position: absolute;
    right: 10px;
    top: 10px;
  }